@media screen and (min-width: 870px) {
  .CookieBanner {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    .cookie-content {
      .cookie-title {
        font-size: 24px;
        line-height: 30px;
      }
      
      .cookie-desc {
        font-size: 16px;
        line-height: 22px;
      }
    }

    .button-wrapper {
      display: flex;
      margin-left: 24px;
      align-self: flex-end;
  
      a,
      button {
        box-sizing: border-box;
        padding: 0 1vm;
        line-height: 44px;
        height: 46px;
        min-width: 140px;
        max-width: 160px;
        white-space: nowrap;
        border-radius: 4px;
        font-size: 16px;
        border: 1px solid rgba(22, 24, 35, .12);
      }
  
      button {
        margin-left: 16px;
      } 
    }
  }
}

@media screen and (max-width: 870px) {
  .CookieBanner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    .cookie-content {
      .cookie-title {
        font-size: 15px;
        line-height: 24px;
      }

      .cookie-desc {
        font-size: 12px;
        line-height: 14px;
      }
    }

    .button-wrapper {
      display: flex;
      // margin-left: 20px;
      margin-top: 5px;
      align-self: flex-end;
  
      a,
      button {
        box-sizing: border-box;
        padding: 0 1vm;
        line-height: 28px;
        height: 24px;
        min-width: 100px;
        white-space: nowrap;
        border-radius: 2px;
        font-size: 12px;
        border: 1px solid rgba(22, 24, 35, .12);
      }
    }
  }
}

.CookieBanner {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  background: #2f3437;
  z-index: 10001;
  color: #fff;
  padding: 2vw 4vw 3vw;
  box-sizing: border-box;
  box-shadow: 0 2px 12px rgba(0, 0, 0, .12);

  p {
    margin: 0 !important;
  }

  .cookie-content {
    text-align: justify;
    .cookie-title {
      font-weight: bolder;
    }

    .cookie-desc {
      margin-top: 8px !important;

      a {
        text-decoration: none;
        color: red;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  
  .button-wrapper {
    a,
    button {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      cursor: pointer;
      background: #fff;
      color: #161823;
      outline: none;
      text-decoration: none;
      border: 1px solid rgba(22, 24, 35, .12);

      &:hover {
        background: #f8f8f8;
      }

      &:active {
        background: #f1f1f1;
      }
    }

    button {
      margin-left: 16px;
      background: #0095ff;
      color: white;

      &:hover {
        background: #0077cc;
      }
    }     
  }
}
